import {BaseContextObject} from "../baseContextObject";

/**
 * Products object is used to work with installed products (check if installed, launch with command line params)
 * Use objects: products
 */
export class ProductsVersion_4_0_0 extends BaseContextObject {

    constructor(context: object) {
        super(context);
    }

    /**
     * Executes specified product
     * @param productCode
     * @param cmdParams
     */
    execute = (productCode: string, cmdParams: string = ''): void => {
        // @ts-ignore
        this.context.products.execute(productCode, cmdParams);
    };

    /**
     * Executes specified product and waits for its execution
     * @param productCode
     * @param cmdParams
     */
    executeAndWait= (productCode: string, cmdParams: string = ''): void => {
        // @ts-ignore
        this.context.products.executeAndWait(productCode, cmdParams);
    };

    /**
     * Retrieves version for specified product code
     * @param productCode
     */
    getVersion = (productCode: string): string => {
        // @ts-ignore
        return this.context.products.getVersion(productCode);
    };

    /**
     * Checks if the product with specified product code is installed on user's system.
     * @param productCode
     */
    isInstalled = (productCode: string): boolean => {
        // @ts-ignore
        return this.context.products.isInstalled(productCode);
    };
}
