import {contextList} from "./data/contextList";
import {Engine} from "./objects/engine/engine";

/**
 * Checks what JS API injected and set engine type, engine version and context
 * Context could be 'window.external' or 'window.chrome.webview2'
 */
export class Initializer {
    context: any = window.external;//default value
    private readonly _contextList: [] = contextList;
    engine: Engine;

    constructor() {

        for (let i = 0; i < this._contextList.length; i++) {
            if (window[this._contextList[i]] !== undefined && this._contextList[i] !== this.context) {
                this.context = window[this._contextList[i]];
                break;
            }
        }

        this.engine = new Engine(this.context);
    }
}
