import {BaseObject} from "../baseObject";
import {Dispatcher} from "../../dispatcher";
import {CallbackObject} from "./callbackObject";
import {DownloaderTray} from "./downloaderTray";
import {DownloaderEngine} from "./downloaderEngine";

/**
 * Downloader wrapper object gives interface to download file from internet and launch binary.
 * It downloads encrypted file and decrypts it with public key to prevent tampering.
 * On object destroy - download is canceled. Download object usually is destroyed on browser window close.
 * Use objects: downloader|tray|download
 * Downloader supported since: 4.0.0
 * Tray supported since: 1.0.0
 */
export class Downloader extends BaseObject {
    private readonly _dispatcher: Dispatcher;

    constructor(context: object, version: string, type: string) {
        super(context, version, type);

        if (type === 'Tray') {
            this.versionObject = new DownloaderTray(context);
        } else {
            this.versionObject = new DownloaderEngine(context, version);
        }

        this._dispatcher = new Dispatcher(this.versionObject);
    }

    /**
     * Launches downloaded binary
     * @param downloadID
     * @param cmdParams
     * Supported since: 4.0.0
     */
    execute = (downloadID: string, cmdParams: string): void => {
        this._dispatcher.dispatch("execute", [downloadID, cmdParams]);
    };

    /**
     * Launches downloaded binary and waits for process end
     * @param downloadID
     * @param cmdParams
     * Supported since: 4.0.0
     */
    executeAndWait = (downloadID: string, cmdParams: string): void => {
        this._dispatcher.dispatch("executeAndWait", [downloadID, cmdParams]);
    };

    /**
     * Cancels a download in progress.
     * @param downloadID
     * Supported since: 4.0.0
     */
    cancel = (downloadID: string): void => {
        this._dispatcher.dispatch("cancel", [downloadID]);
    };

    /**
     * Wrapper function to do simple download
     * @param urlObject
     * @param cmdParams
     * @param clientCallback
     * Supported since: 4.0.0
     */
    downloadAndLaunch = (urlObject: {} = {url: "", secureUrl: ""}, cmdParams: string = '', clientCallback?: object): string | void => {
        return this._dispatcher.dispatch("downloadAndLaunch", [urlObject, cmdParams, clientCallback]);
    };

    /**
     * Helper function for client.
     * Creating Callback Instance.
     * Client able to create callback object
     * and may redefine necessary methods
     * and pass it through downloadAndLaunch method e.g.
     *
     * //IE friendly code
     * var clientCallback = window.desktopApi.downloader.createCallbackInstance();
     * clientCallback.onProgress = function(progressValue){console.log(progressValue);}
     * var downloadId = window.desktopApi.downloader.downloadAndLaunch(url, cmdParam, clientCallback);
     */
    createCallbackInstance = (): CallbackObject => {
        return this._dispatcher.dispatch("createCallbackInstance", []);
    };
}
