import {versionCompare} from "../../utils/versioncompare";
import {WindowVersion_4_0_0} from "./windowVersion_4_0_0";
import {WindowVersion_3_2_0} from "./windowVersion_3_2_0";
import {WindowVersion_3_1_0} from "./windowVersion_3_1_0";
import {WindowVersion_1_0_0} from "./windowVersion_1_0_0";

/**
 * Helper Wrapper Class
 * Check version and return versioned class instance
 */
export class WindowEngine {
    constructor(context: object, version: string) {

        if (versionCompare(version, '4.0.0', '>=')) {
            return new WindowVersion_4_0_0(context);
        } else if (version === '3.2.0') {
            return new WindowVersion_3_2_0(context);
        } else if (version === '3.1.0') {
            return new WindowVersion_3_1_0(context);
        } else {
            return new WindowVersion_1_0_0(context);
        }

    }
}
