import {Products} from "../objects/products/products";
import {AppInfo} from "../objects/appinfo/appInfo";
import {Window} from "../objects/window/window";
import {Utils} from "../objects/utils/utils";
import {Engine} from "../objects/engine/engine";
import {Downloader} from "../objects/downloader/downloader";

const classes: any = {Utils: Utils, AppInfo:AppInfo, Window: Window, Products:Products, Engine:Engine, Downloader:Downloader};

/**
 * createInstance helps with dynamic creation class instances by class name
 * @param name
 */
export function createInstance(name: string) {
    return classes[name];
}
