import {versionCompare} from "../../utils/versioncompare";
import {DownloaderVersion_4_0_0} from "./downloaderVersion_4_0_0";
import {DownloaderVersion_1_0_0} from "./downloaderVersion_1_0_0";

/**
 * Helper Wrapper Class
 * Check version and return versioned class instance
 */
export class DownloaderEngine {

    constructor(context: object, version: string) {

        if (versionCompare(version, '4.0.0', '>=')) {
            return new DownloaderVersion_4_0_0(context);
        } else {
            return new DownloaderVersion_1_0_0(context);
        }

    }
}
