import {BaseObject} from "../baseObject";
import {Dispatcher} from "../../dispatcher";
import {ProductsEngine} from "./productsEngine";

/**
 * Products object is used to work with installed products (check if installed, launch with command line params)
 * Use objects: products|system|process
 * Supported since: 4.0.0
 */
export class Products extends BaseObject {
    private readonly _dispatcher: Dispatcher;

    constructor(context: object, version: string, type: string) {
        super(context, version, type);
        this.versionObject = new ProductsEngine(context, version);
        this._dispatcher = new Dispatcher(this.versionObject);
    }

    /**
     * Executes specified product
     * @param productCode
     * @param cmdParams
     * Since: 4.0.0
     */
    //todo it is not clear where it can be used
    execute = (productCode: string, cmdParams: string = ''): void => {
        this._dispatcher.dispatch("getApplicationVersion", [productCode, cmdParams]);
    };

    /**
     * Executes specified product and waits for its execution
     * @param productCode
     * @param cmdParams
     * Since: 4.0.0
     */
    //todo it is not clear where it can be used
    executeAndWait = (productCode: string, cmdParams: string = ''): void => {
        this._dispatcher.dispatch("executeAndWait", [productCode, cmdParams]);
    };

    /**
     * Retrieves version for specified product code
     * @param productCode
     * Since: 4.0.0
     */
    getVersion = (productCode: string): string => {
        return this._dispatcher.dispatch("getVersion", [productCode]);
    };

    /**
     * Checks if the product with specified product code is installed on user's system.
     * @param productCode
     * Since: 4.0.0
     */
    isInstalled = (productCode: string): boolean => {
        return this._dispatcher.dispatch("isInstalled", [productCode]);
    };

    /**
     * Finds the main executable file of the application with the specified product code.
     * @param productCode
     * Supported since: 1.0.0
     * removed since 4.0.0
     */
    getMainExecutablePath = (productCode: string): string => {
        return this._dispatcher.dispatch("getMainExecutablePath", [productCode]);
    };
}
