/**
 * Dispatch method call with scope context and arguments
 */
export class Dispatcher {
    private readonly _versionObject: any;

    constructor(versionObject: any) {
         this._versionObject = versionObject;
    }

    dispatch = (func: any, args: any) => {
        if(this._hasFunction(func)) {
            return this._versionObject[func].call(this._versionObject, args);
        } else {
            throw new Error('Function not exist.');
        }
    };

    private _hasFunction = (func: any) => {
        return (typeof this._versionObject[func] === "function");
    };
}
