import {versionCompare} from "../../utils/versioncompare";
import {ProductsVersion_4_0_0} from "./productsVersion_4_0_0";
import {ProductsVersion_1_0_0} from "./productsVersion_1_0_0";

/**
 * Helper Wrapper Class
 * Check version and return versioned class instance
 */
export class ProductsEngine {

    constructor(context: object, version: string) {

        if (versionCompare(version, '4.0.0', '>=')) {
            return new ProductsVersion_4_0_0(context);
        } else {
            return new ProductsVersion_1_0_0(context);
        }

    }
}
