import {BaseContextObject} from "../baseContextObject";

/**
 * Utils object provide application wide common functionality
 * Use objects: tray
 */
export class UtilsTray extends BaseContextObject {

    constructor(context: object) {
        super(context);
    }

    /**
     * Opens url in default browser currently configured on user's system.
     * @param url
     */
    openInDefaultBrowser = (url: string): void => {
        // @ts-ignore
        this.context.tray.openInDefaultBrowser(url);
    };
}
