export class Basewnd {
  constructor(
    public DPI: number = 100,
    public isMinimizeSupport: boolean = true,
  ) {}

  hide(): void {
    console.log('basewnd hide method called');
  }

  close(): void {
    console.log('basewnd close method called');
  }

  closeWithExitCode(exitCode: number): void {
    console.log(
      `basewnd closeWithExitCode method with exitCode: ${exitCode} called`,
    );
  }

  show(width: number, height: number): void {
    console.log(
      `basewnd show method with width: ${width} and height: ${height} called`,
    );
  }

  getDPI(): number {
    return this.DPI;
  }

  minimize(): void {
    console.log('basewnd minimize method called');
  }

  isMinimizeSupported(): boolean {
    return this.isMinimizeSupport;
  }
}

export const basewndInstance = new Basewnd();
