import {BaseObject} from "../baseObject";
import {Dispatcher} from "../../dispatcher";
import {UtilsTray} from "./utilsTray";
import {UtilsEngine} from "./utilsEngine";

/**
 * Utils object provide application wide common functionality
 * Use objects: system|tray
 * Supported since: 1.0.0
 */
export class Utils extends BaseObject {
    private readonly _dispatcher: Dispatcher;

    constructor(context: object, version: string, type: string) {
        super(context, version, type);

        if (type === 'Tray') {
            this.versionObject = new UtilsTray(context);
        } else {
            this.versionObject = new UtilsEngine(context, version);
        }

        this._dispatcher = new Dispatcher(this.versionObject);
    }

    /**
     * Opens url in default browser currently configured on user's system.
     * @param url
     */
    openInDefaultBrowser = (url: string): void => {
        return this._dispatcher.dispatch("openInDefaultBrowser", [url]);
    };
}
