/**
 * Base class for inheritance
 */
export class BaseObject {
    [index: string]: any;

    type: string;
    version: string;
    context: object;
    versionObject: any = {};

    constructor(context: object, version: string, type: string ) {
        this.context = context;
        this.version = version;
        this.type = type;
    }

    /**
     * Return all versioned class methods
     * Helpful to have only available methods
     * in the global namespace
     * e.g. window.desktopApi.Window[method_name]
     */
    getMethods = () => {
        let methods: any = {},
            /**
             * To be compatible with old IE
             * this can be done in other way to not use polyfills for
             * `Object.getOwnPropertyNames` and `Array.filter`
            */
            //todo redone for old IE to exclude polyfills
            methodNames = Object.getOwnPropertyNames(this.versionObject).filter(item => typeof this.versionObject[item] === 'function');

        for (let i = 0; i < methodNames.length; i++) {
            methods[methodNames[i]] = this.versionObject[methodNames[i]];
        }

        return methods;
    }
}
